$yellow: #EBDD15;
$blue: #00D6D5;
$green: #AAF63D;
$pink: #D818F5;
$black: #242424;
$white: #FAFAFA;
$firstTransition: 0.5s;
$secondTransition: 1.8s;


.mainWrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.upperBar {
  width: 100%;
  background-image: linear-gradient(rgba($black, 0.8), rgba($black, 0));
  flex: 0 0 50px;
  transform: translateY(-100%);

  animation: reveal-2 0.3s ease $secondTransition forwards;

  &.old {
    background-image: linear-gradient(to bottom, red,orange,yellow,green,blue,indigo,violet);
  }
}

.nameWrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  justify-content: center;
  
  h1 {
    z-index: 2;
    position: absolute;
    width: 100%;
    font-size: 70px;
    margin: 0;
    text-align: center;

    @media (min-width: 750px) {
      font-size: 110px;
    }

    @media (min-width: 1100px) {
      font-size: 165px;
    }
  }

  &.old {
    h1 {
      font-family: 'Courier New', Courier, monospace;
      text-align: left;

      animation: banner 4s linear infinite;
    }
  }
}

.firstname,
.lastname {
  position: relative;
  display: flex;
  align-items: center;
  height: 100px;

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: inherit;
    opacity: 1;
  }

  &.old {
    border: 3px dashed red;
    height: 85px;
    width: 300px;
    margin: 0 0 10px 0;
    overflow: hidden;

    &::after {
      background-color: white;
      animation: unset;
    }
  }

  @media (min-width: 750px) {
    height: 170px;

    &.old {
      height: 125px;
      width: 600px;
    }
  }

  @media (min-width: 1100px) {
    height: 270px;

    &.old {
      height: 200px;
      width: 900px;
    }
  }
}

.firstname {
  width: 375px;

  &::after {
    animation: reveal $firstTransition ease-out forwards;
  }

  @media (min-width: 750px) {
    width: 600px;
  }

  @media (min-width: 1100px) {
    width: 900px;
  }
}

.lastname {
  width: 400px;

  &::after {
    animation: reveal $firstTransition ease-out $firstTransition forwards;
  }

  @media (min-width: 750px) {
    width: 700px;
  }

  @media (min-width: 1100px) {
    width: 1100px;
  }
}

.firstBrush {
  width: 100%;
}

.secondBrush {
  width: 100%;
}

.lowerBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  width: 100%;
  background-image: linear-gradient(rgba($black, 0), rgba($black, 0.8));
  flex: 0 0 100px;
  transform: translateY(100%);
  padding: 0 10px;

  animation: reveal-3 0.3s ease $secondTransition forwards;

  &.old {
    background-image: unset;

    .lowerContentWrapper > svg {
      fill: transparent;
    }

    .lowerContentWrapper > span {
      border: 2px solid gray;
      background-color: #EFEFEF;
      padding: 5px 15px;
      font-family: 'Times New Roman', Times, serif;
    }

    .lowerContentWrapper > a {
      font-family: 'Times New Roman', Times, serif;
      text-decoration: underline;
      color: blue;
      padding: 0;
    }
  }

  @media (min-width: 750px) {
    padding: 0 25px;
  }

  @media (min-width: 1100px) {
    height: 150px;
    flex: 0 0 150px;
    padding: 0 75px;
  }
}

.lowerContentWrapper {
  display: flex;
  justify-content: center;
  align-items: center;

  svg {
    fill: $yellow;
    width: 120px;

    @media (min-width: 750px) {
      width: 170px;
    }
  }

  a,
  span {
    position: absolute;
    padding: 0 0 15px 10px;
    color: $black;
    text-decoration: none;
  }
}

@keyframes banner {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-150%);
  }
}

@keyframes reveal {
  0% {
    transform: translateX(0%);
  }

  99% {
    transform: translateX(100%);
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes reveal-2 {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(0%);
  }
}

@keyframes reveal-3 {
  0% {
    transform: translateY(100%);
  }

  100% {
    transform: translateY(0%);
  }
}



